/**
 * API client for making authenticated requests to the FastAPI backend.
 *
 * Integrates with Better Auth to automatically include JWT tokens
 * in the Authorization header. Handles authentication errors gracefully.
 */

import { authClient } from "./auth";

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8001";

/**
 * Custom error class for API errors with structured error details
 */
export class ApiError extends Error {
  constructor(
    public status: number,
    public statusText: string,
    public data?: any
  ) {
    super(`API Error ${status}: ${statusText}`);
    this.name = "ApiError";
  }
}

/**
 * Get JWT token from Better Auth session.
 *
 * Better Auth manages sessions and provides the token through its API.
 * This works in both client and server contexts.
 */
async function getAuthToken(): Promise<string | null> {
  try {
    // Get session from Better Auth client
    const session = await authClient.getSession();

    // Extract token from session data
    if (session?.data?.session?.token) {
      return session.data.session.token;
    }

    return null;
  } catch (error) {
    console.error("Failed to get auth token:", error);
    return null;
  }
}

/**
 * Make an authenticated API request.
 *
 * @param endpoint - API endpoint path (e.g., "/tasks")
 * @param options - Fetch options (method, body, headers, etc.)
 * @returns Response data
 * @throws ApiError if request fails
 */
export async function apiRequest<T>(
  endpoint: string,
  options: RequestInit = {}
): Promise<T> {
  // Get JWT token from Better Auth
  const token = await getAuthToken();

  if (!token) {
    // No token available - redirect to login
    if (typeof window !== "undefined") {
      window.location.href = "/login";
    }
    throw new ApiError(401, "Authentication required");
  }

  // Prepare headers with Authorization bearer token
  const headers: HeadersInit = {
    "Content-Type": "application/json",
    Authorization: `Bearer ${token}`,
    ...options.headers,
  };

  try {
    // Make the API request
    const response = await fetch(`${API_BASE_URL}${endpoint}`, {
      ...options,
      headers,
    });

    // Handle 204 No Content (successful DELETE)
    if (response.status === 204) {
      return undefined as T;
    }

    // Handle 401 Unauthorized - token expired or invalid
    if (response.status === 401) {
      // Redirect to login page
      if (typeof window !== "undefined") {
        window.location.href = "/login";
      }
      throw new ApiError(401, "Authentication expired. Please sign in again.");
    }

    // Parse response body
    let data: any;
    const contentType = response.headers.get("content-type");
    if (contentType?.includes("application/json")) {
      data = await response.json();
    } else {
      data = await response.text();
    }

    // Handle other error responses
    if (!response.ok) {
      throw new ApiError(response.status, response.statusText, data);
    }

    return data as T;
  } catch (error) {
    // Re-throw ApiError as-is
    if (error instanceof ApiError) {
      throw error;
    }
    // Wrap other errors
    if (error instanceof Error) {
      throw new ApiError(500, error.message);
    }
    throw new ApiError(500, "An unexpected error occurred");
  }
}

/**
 * Convenience methods for common HTTP verbs.
 */
export const api = {
  /**
   * GET request
   */
  get: <T>(endpoint: string) => apiRequest<T>(endpoint, { method: "GET" }),

  /**
   * POST request
   */
  post: <T>(endpoint: string, data: unknown) =>
    apiRequest<T>(endpoint, {
      method: "POST",
      body: JSON.stringify(data),
    }),

  /**
   * PUT request
   */
  put: <T>(endpoint: string, data: unknown) =>
    apiRequest<T>(endpoint, {
      method: "PUT",
      body: JSON.stringify(data),
    }),

  /**
   * DELETE request
   */
  delete: <T>(endpoint: string) =>
    apiRequest<T>(endpoint, { method: "DELETE" }),
};

export default api;
